<template>
  <div class="foodMenu">
    <div class="header">
      <div class="iconfont icon-jiantouzuo goBack-icon" @click="gotoback()"></div>
      <div class="tp-search">
        <div class="tp-search-box">
          <div class="iconfont icon-sousuo"></div>
          <input
            type="text"
            v-model="tableNo"
            class="inputCla"
            @focus="gotoSearch()"
            placeholder="菜品检索"
          >
        </div>
      </div>
    </div>
    <div class="body">
      <div class="left-container">
        <div class="vegetables-list">
          <div
            class="vegetables-item"
            :class="[item.chose?'vegetables-item-active':'']"
            v-for="(item, index) in vegetablesList"
            :key="index"
            @click="vegetablesListClickFun(item,index)"
          >
            <div class="vegetables-item-icon"></div>
            <div class="vegetables-item-text">{{item.sortDescript}}</div>
          </div>
        </div>
      </div>
      <div class="right-container">
        <!-- <div
          class="dish-name-list"
          v-infinite-scroll="loadMore"
          infinite-scroll-disabled="loading"
          infinite-scroll-distance="10"
        >-->
        <div class="dish-name-list">
          <div class="dish-name-item" v-for="(item, index) in dishList" :key="index">
            <div class="dish-name-left">
              <p class="dish-name-left-up">{{item.pluDescript}}</p>
              <p class="dish-name-left-down">
                <span class="dish-name-price">￥{{item.pluPrice}}</span>/ 位
              </p>
            </div>
            <div class="dish-name-right">
              <template v-if="item.itemClasslist.length>0">
                <div class="taste-btn icon-btn" @click="tasteClickFun(item)">
                  口味
                  <span v-if="item.num>0" class="taste-btn-tips">{{item.num}}</span>
                </div>
              </template>
              <template v-else-if="item.num>0">
                <div class="cut-btn" @click="cutNumFun(item,index)">
                  <i class="iconfont icon-jian"></i>
                </div>
                <span class="num">{{item.num}}</span>
                <div class="add-btn icon-btn" @click="addNumFun(item,index)">
                  <i class="iconfont icon-jia1"></i>
                </div>
              </template>
              <template v-else>
                <div class="add-btn icon-btn" @click="addNumFun(item,index)">
                  <i class="iconfont icon-jia1"></i>
                </div>
              </template>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="foot">
      <div class="shopping-cart" @click="okFun()">
        <i class="iconfont icon-gouwuche"></i>
        <span class="shopping-num">{{cartTotalNum}}</span>
      </div>
      <div class="confirm-btn" @click="okFun()">选好了</div>
      <div class="total-price">
        <span class="total-price-tips">总计：</span>
        <span class="total-price-num-con">
          <span class="total-price-unit">￥</span>
          <span class="total-price-num">{{totalPrice}}</span>
        </span>
      </div>
    </div>
    <foodTaste v-if="isShowTaste" :itemTasteObj="itemTasteObj" @hidepage="hidepage($event)"></foodTaste>
  </div>
</template>
<script>
import foodTaste from "../common/foodTaste";
import { mapState } from "vuex";
import { Indicator, Toast } from "mint-ui";
export default {
  components: {
    foodTaste
  },
  data() {
    return {
      tableNo: "",
      showDelet: false,
      vegetablesList: [],
      dishList: [],
      cacheMenu: {},
      choseIndex: 0,
      SelectedItemArr: [],
      pageNum: 1,
      stopLearnMore: true,
      isShowTaste: false,
      itemTasteObj: {},
      cartTotalNum: 0,
      totalPrice: 0
    };
  },
  computed: {
    ...mapState({
      seatsInfo: state => state.food.seatsInfo,
      MenuArr: state => state.food.MenuArr
    })
  },
  created() {
    this.$http
      .get("/ohotel/router", {
        params: {
          method: "getPosPluSort",
          pccode: this.seatsInfo.pccode
        },
        vv: 5
      })
      .then(res => {
        // console.log("denglu:", res);
        if (res.data.code == "0") {
          if (res.data.data) {
            this.vegetablesList = res.data.data;
            this.vegetablesList[0].chose = true;
            this.vegetablesList.forEach((element, index) => {
              if (index == 0) {
                this.$set(element, "chose", true);
              } else {
                this.$set(element, "chose", false);
              }
            });
            // this.stopLearnMore = false;
            this.getAjaxDish(this.vegetablesList[0], 1);
          }
        } else {
          this.msg(res.data.msg);
        }
      });
  },
  methods: {
    gotoback() {
      this.$router.push("/foodCreateOrder");
    },
    gotoSearch() {
      this.$router.push("/foodMenuSearch");
    },
    con(sign, arr) {
      let str = sign;
      arr.forEach(item => {
        str += item.pluDescript + "," + item.num + " ";
      });
      return str;
    },
    okFun() {
      // console.log("this.SelectedItemArr:", this.SelectedItemArr);
      this.SelectedItemArr = this.SelectedItemArr.filter(data => {
        return data.num > 0;
      });

      // console.log("this.SelectedItemArr:", this.SelectedItemArr);

      if (this.SelectedItemArr.length > 0) {
        this.$store.commit("submitCart", this.SelectedItemArr);
        this.$router.push("/foodShoppingCart");
      } else {
        this.msg("请选择菜品!");
      }
    },
    cutNumFun(item, indexs) {
      this.dishList[indexs].num--;
      this.SelectedItemArr.forEach(ele => {
        if (ele.pluCode == this.dishList[indexs].pluCode) {
          ele.num--;
        }
      });
      this.cacheMenu[this.dishList[indexs].sortCode] = this.dishList;
    },
    addNumFun(item, indexs) {
      this.dishList[indexs].num++;
      let isIn = false;
      this.SelectedItemArr.forEach(ele => {
        if (ele.pluCode == this.dishList[indexs].pluCode) {
          ele.num++;
          isIn = true;
        }
      });
      if (!isIn) {
        this.SelectedItemArr.push(_.cloneDeep(this.dishList[indexs]));
      }
      this.cacheMenu[this.dishList[indexs].sortCode] = this.dishList;
    },
    tasteClickFun(item, index) {
      this.itemTasteObj = item;
      this.isShowTaste = true;
    },
    hidepage(val) {
      this.isShowTaste = false;
      let isIn = false;

      if (val) {
        console.log(val);
        this.SelectedItemArr.forEach(ele => {
          if (
            ele.pluCode == val.pluCode &&
            _.isEqual(ele.itemClasslist, val.itemClasslist)
          ) {
            ele.num += val.num;
            isIn = true;
          }
        });
        if (!isIn) {
          this.SelectedItemArr.push(_.cloneDeep(val));
        }

        this.dishList.forEach((ele, index) => {
          if (ele.pluCode == val.pluCode) {
            ele.num += val.num;
          }
        });
        this.cacheMenu[val.sortCode] = this.dishList;
      }
    },
    vegetablesListClickFun(item, index) {
      if (index == this.choseIndex) {
        return;
      }
      this.pageNum = 1;
      this.dishList = [];
      this.vegetablesList[this.choseIndex].chose = false;
      this.vegetablesList[index].chose = true;
      this.choseIndex = index;
      if (this.cacheMenu.hasOwnProperty(item.sortCode)) {
        this.dishList = this.cacheMenu[item.sortCode];
      } else {
        this.getAjaxDish(item, 1);
      }
    },
    // loadMore() {
    //   if (this.stopLearnMore == false) {
    //     this.getAjaxDish(this.vegetablesList[this.choseIndex], this.pageNum++);
    //   }
    // },
    getAjaxDish(item, page) {
      console.log("item222", item);
      this.$http
        .get("/ohotel/router", {
          params: {
            method: "getPosInterfacePluAll",
            pccode: this.seatsInfo.pccode,
            sortCode: item.sortCode,
            needPage: "false"
            // curPage: page
            // pageSize: 10
          },
          vv: 5
        })
        .then(res => {
          // console.log("denglu:", res);
          if (res.data.code == "0") {
            if (res.data.data) {
              this.SelectedItemArr = this.MenuArr;
              //console.log(" this.SelectedItemArr:0", this.SelectedItemArr);
              res.data.data.forEach((ele, indexs) => {
                let isIn = false;
                this.MenuArr.forEach(element => {
                  // console.log(ele.pluCode, element.pluCode);
                  if (ele.pluCode == element.pluCode) {
                    //this.$set(ele, "num", element.num);
                    if (ele.itemClasslist.length > 0) {
                      if (!ele.num) {
                        ele.num = 0;
                      }
                      ele.num += element.num;
                    } else {
                      res.data.data[indexs] = _.cloneDeep(element);
                    }

                    isIn = true;
                  }
                });
                if (!isIn) {
                  this.$set(ele, "num", 0);
                }
              });
              console.log("res.data.data", res.data.data);
              this.dishList.push(...res.data.data);
              this.cacheMenu[this.dishList[0].sortCode] = this.dishList;
            }
          } else {
            this.msg(res.data.msg);
          }
        });
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  },
  watch: {
    SelectedItemArr: {
      handler(val) {
        console.log("watch:Selected", val);
        this.cartTotalNum = 0;
        val.forEach(ele => {
          this.cartTotalNum += ele.num;
        });
        this.totalPrice = 0;
        val.forEach(ele => {
          if (ele.itemClasslist.length > 0) {
            let SideDishPrice = 0;

            ele.itemClasslist.forEach(element => {
              if (element.price) {
                SideDishPrice += Number.parseFloat(element.price);
              }
            });
            this.totalPrice +=
              Number.parseFloat(SideDishPrice + ele.pluPrice) * ele.num;
          } else {
            this.totalPrice += Number.parseFloat(ele.pluPrice) * ele.num;
          }
        });
        this.totalPrice = this.totalPrice.toFixed(2);
        this.$store.commit("submitTotalPrice", this.totalPrice);
      },
      deep: true
    }
  }
};
</script>
<style lang="less" scoped>
.foodMenu {
  position: relative;
  height: 100vh;
  .header {
    padding-left: 0.347rem;
    padding-right: 11px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #393c41;
    .goBack-icon {
      color: #fff;
    }
    .tp-search {
      flex: 1;
      display: flex;
      justify-content: space-between;
      flex-direction: row;
      padding-left: 0.373rem;
      background-color: #393c41;

      .tp-search-box {
        flex: 1;
        height: 0.853rem;
        margin-top: 0.211rem;
        margin-bottom: 0.211rem;
        display: flex;
        justify-content: flex-start;
        flex-direction: row;
        align-items: center;
        background: #f6f8f9;
        border-radius: 1.3rem;
        .icon-sousuo {
          color: #cccccc;
          margin-left: 0.27rem;
          font-size: 0.37rem;
        }
        .icon-quxiao {
          color: #cccccc;
          font-size: 0.32rem;
          margin-right: 0.27rem;
        }

        .inputCla {
          border: none;
          background: #f6f8f9;
          outline: none;
          font-size: 0.32rem;
          width: 85%;
          margin-left: 0.27rem;
        }
      }
    }
  }
  .body {
    position: absolute;
    top: 53px;
    bottom: 52px;
    width: 100%;
    display: flex;
    .left-container {
      width: 2.587rem;
      height: 95%;
      margin-right: 4px;
      background-color: #fff;
      overflow: auto;
      .vegetables-item {
        height: 1.067rem;
        font-size: 0.373rem;
        color: #393c41;
        display: flex;
        .vegetables-item-icon {
          width: 3px;
          height: 100%;
          margin-right: 0.213rem;
          background-color: #fff;
        }
        .vegetables-item-text {
          flex: 1;
          text-align: left;
          line-height: 1.067rem;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
      }

      .vegetables-item-active {
        background-color: #f6f8f9;
        .vegetables-item-icon {
          background-color: #f69c2d;
        }
      }
    }
    .right-container {
      flex: 1;
      height: 100%;
      overflow: auto;
      .dish-name-list {
        .dish-name-item {
          display: flex;
          justify-content: space-between;
          align-items: center;
          background-color: #fff;
          margin-bottom: 0.133rem;
          padding-right: 0.32rem;
          .dish-name-left {
            padding: 0.133rem 0.32rem 0.133rem 6px;

            .dish-name-left-up {
              font-size: 0.427rem;
              color: #333333;
              text-align: left;
            }
            .dish-name-left-down {
              text-align: left;
              font-size: 0.32rem;
              color: #a2a8bd;
              display: flex;
              align-items: center;
              .dish-name-price {
                font-size: 0.427rem;
                color: #f7544d;
                margin-right: 11px;
              }
            }
          }
          .dish-name-right {
            width: 2.4rem;
            display: flex;
            align-items: center;
            justify-content: flex-end;
            .icon-btn {
              color: #fff;
              background: #ff7b02;
            }
            .add-btn {
              width: 0.507rem;
              height: 0.507rem;
              line-height: 0.507rem;
              border-radius: 2.667rem;
              .iconfont {
                font-size: 0.32rem;
              }
            }
            .cut-btn .iconfont {
              font-size: 0.48rem;
              color: #ff7b02;
              font-weight: 800;
            }
            .num {
              display: inline-block;
              width: 33px;
              font-size: 19.2px;
              color: #77a0ff;
            }

            .taste-btn {
              width: 38px;
              height: 0.64rem;
              font-size: 0.32rem;
              line-height: 0.64rem;
              border-radius: 2.667rem;
              position: relative;
              .taste-btn-tips {
                position: absolute;
                top: -0.267rem;
                right: -0.133rem;
                display: inline-block;
                width: 0.453rem;
                height: 0.453rem;
                line-height: 0.453rem;
                border-radius: 0.267rem;
                background: #d0021b;
              }
            }
          }
        }
      }
    }
  }
  .foot {
    position: fixed;
    bottom: 0px;
    height: 1.33rem;
    width: 100%;

    background-color: #fff;
    border-top: 1px solid #e9ecf1;
    .shopping-cart {
      position: absolute;
      top: -0.96rem;
      left: 0.133rem;
      width: 86px;
      height: 1.33rem;
      padding-top: 0.533rem;
      background: url("../../assets/img/food-shoppingcart-bg.png") no-repeat;
      background-size: contain;

      .iconfont {
        font-size: 0.96rem;
      }
      .shopping-num {
        position: absolute;
        right: 0.347rem;
        width: 0.453rem;
        height: 0.453rem;
        line-height: 0.453rem;
        color: #fff;
        border-radius: 2.667rem;
        background-color: #d0021b;
      }
    }
    .total-price {
      float: right;

      margin-right: 0.267rem;
      height: 100%;
      display: flex;
      align-items: center;
      .total-price-tips {
        font-size: 0.32rem;
        color: #a2a8bd;
      }
      .total-price-num-con {
        color: #f7544d;
        font-size: 0;
        .total-price-unit {
          font-size: 0.32rem;
        }
        .total-price-num {
          font-size: 0.64rem;
        }
      }
    }
    .confirm-btn {
      float: right;
      height: 100%;
      line-height: 3;
      width: 120px;
      font-size: 0.427rem;
      color: #ffffff;
      background-image: linear-gradient(-90deg, #f6a129 0%, #ff6e61 98%);
    }
  }
}
</style>



