<template>
  <div class="foodTaste">
    <div class="foodTaste-container">
      <div class="header">
        <span>口味</span>
        <i class="iconfont icon-cuowu" @click="hidepage('cancel')"></i>
      </div>
      <div class="body">
        <div class="title">
          <span class="name">{{itemObj.pluDescript}}</span>
          <span class="price">
            <span class="num">￥{{itemObj.pluPrice}}</span>/ 位
          </span>
        </div>
        <div class="quantity-container">
          <div class="quantity-tips">选择数量：</div>
          <div class="quantity-operating">
            <i class="iconfont icon-jian" @click="cutFun()"></i>
            <span class="num">{{itemObj.num}}</span>
            <div class="add-btn">
              <i class="iconfont icon-jia1" @click="addFun()"></i>
            </div>
          </div>
        </div>
        <div class="taste-container">
          <div
            class="taste-content"
            v-for="(itemlist, indexs) in itemObj.itemClasslist"
            :key="indexs"
          >
            <div class="taste-tips">{{itemlist.itemClassName}}：</div>
            <div class="taste-details">
              <span
                class="taste-desc"
                v-for="(item, index) in itemlist.pluCondgplist"
                :key="index"
                :class="[item.chose?'taste-desc-active':'']"
                @click="tipClickFun(item,index,indexs)"
              >{{item.descript}}</span>
              <!-- <span class="taste-desc">红烧</span>
              <span class="taste-desc taste-desc-active">红烧</span>
              <span class="taste-desc">红烧</span>
              <span class="taste-desc">红烧</span>-->
            </div>
          </div>
        </div>
      </div>
      <div class="foot">
        <div class="btn cancel-btn" @click="hidepage('cancel')">取 消</div>
        <div class="btn confirm-btn" @click="hidepage('ok')">确 认</div>
      </div>
    </div>
    <div class="foodTaste-bg"></div>
  </div>
</template>
<script>
import { Indicator, Toast } from "mint-ui";
export default {
  props: {
    itemTasteObj: Object
  },
  data() {
    return {
      itemObj: _.cloneDeep(this.itemTasteObj)
    };
  },
  mounted() {
    console.log(" this.itemObj:", this.itemObj);
    this.itemObj.itemClasslist.forEach(element => {
      element.pluCondgplist.forEach((ele, index) => {
        if (index == 0) {
          this.$set(ele, "chose", true);
        } else {
          this.$set(ele, "chose", false);
        }
      });
    });

    this.itemObj.num = 1;
  },
  methods: {
    hidepage(val) {
      if (val == "ok") {
        // if (this.itemObj.num == 0) {
        //   this.msg("请选择数量！");
        //   return;
        // }
        this.$emit("hidepage", this.itemObj);
      } else {
        this.itemObj.num = 0;
        this.$emit("hidepage", "");
      }
    },
    cutFun() {
      if (this.itemObj.num > 0) {
        this.itemObj.num--;
      }
    },
    addFun() {
      this.itemObj.num++;
    },
    tipClickFun(item, index, indexs) {
      // console.log(this.itemObj);
      this.itemObj.itemClasslist[indexs].pluCondgplist.forEach(
        (element, inde) => {
          if (inde == index) {
            element.chose = true;
          } else {
            element.chose = false;
          }
        }
      );
    },
    msg(msg) {
      Toast({
        message: msg,
        position: "bottom",
        duration: 3000
      });
    }
  }
};
</script>
<style lang="less" scoped>
.foodTaste {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9;
  width: 100%;
  height: 100%;
  .foodTaste-container {
    position: absolute;
    bottom: 0px;
    z-index: 1;
    width: 100%;
    background-color: #fff;
    .header {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin: 0 0.427rem;
      height: 1.33rem;
      border-bottom: 1px solid #dddddd;
    }
    .body {
      padding: 0 0.427rem;
      .title {
        padding-top: 0.427rem;
        display: flex;
        justify-content: space-between;
        .name {
          font-size: 0.533rem;
          color: #333333;
        }
        .price {
          font-size: 0.373rem;
          color: #a2a8bd;
          .num {
            font-size: 0.533rem;
            color: #f7544d;
            margin-right: 12.4px;
          }
        }
      }
      .quantity-container {
        display: flex;
        align-items: center;
        margin: 0.533rem 0;
        .quantity-tips {
          width: 70px;
          margin-right: 0.213rem;
          font-size: 0.373rem;
          color: #a2a8bd;
        }
        .quantity-operating {
          display: flex;
          align-items: center;
          .icon-jian {
            color: #ff7b02;
            font-weight: bold;
            font-size: 0.48rem;
          }
          .num {
            width: 1.33rem;
            font-size: 19.2px;
            color: #77a0ff;
          }
          .add-btn {
            background-color: #ff7b02;
            color: #fff;
            border-radius: 2.667rem;
            width: 0.453rem;
            height: 0.453rem;
            line-height: 0.453rem;
            .icon-jia1 {
              font-size: 0.32rem;
            }
          }
        }
      }
      .taste-container {
        height: 8rem;
        overflow-y: scroll;
        .taste-content {
          display: flex;
          align-items: center;
          .taste-tips {
            width: 70px;
            text-align: left;
            font-size: 0.373rem;
            color: #a2a8bd;
            margin-right: 0.213rem;
            word-break: break-word;
          }
          .taste-details {
            text-align: left;
            flex: 1;
            .taste-desc {
              display: inline-block;
              font-size: 0.373rem;
              padding: 0.16rem 0.67rem;
              border: 1px solid #ffdfc2;
              border-radius: 3px;
              background-color: #fff9f3;
              margin-right: 9px;
              margin-bottom: 0.267rem;
            }
            .taste-desc-active {
              background: #ff7b02;
              color: #fff;
            }
          }
        }
      }
    }
    .foot {
      border-top: 1px solid #e9ecf1;
      display: flex;
      align-items: center;
      .btn {
        flex: 1;
        font-size: 0.427rem;
        height: 1.33rem;
        line-height: 1.33rem;
      }
      .cancel-btn {
        background-color: #fff;
      }
      .confirm-btn {
        background-image: linear-gradient(-90deg, #f6a129 0%, #ff6e61 98%);
        color: #ffffff;
      }
    }
  }
  .foodTaste-bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0.5;
    background: #202025;
  }
}
</style>



